import React from 'react';
import './JunqiGame.css'; 
import BoardCell from './BoardCell';

const oppositeColorMap = {
    'O': 'G',
    'B': 'P',
    'G': 'O',
    'P': 'B'
};

const leftColorMap = {
    'O': 'B',
    'B': 'G',
    'G': 'P',
    'P': 'O'
};

const rightColorMap = {
    'O': 'P',
    'B': 'O',
    'G': 'B',
    'P': 'G'
};

const DEFAULT_BOARD_WIDTH = 834;
const DEFAULT_FORMATION_WIDTH = 281;

const railOuterColor = "rgb(104,144,176)";
const railInnerColor = "rgb(200,236,240)";

export default class JunqiGame extends React.Component {

    constructor(props) {
        super(props);


        let formationCells = {};
        let edges = {};
        let defaultFormation = [9,1,6,3,3,5,6,7,8,1,7,10,4,2,3,5,1,2,100,10,2,100,100,0,4];
        // let defaultFormation = [9,9,9,9,9,9,9,1,1,1,9,10,1,1,1,9,9,9,100,10,9,100,100,0,4];
        let numRows = 6;
        let numCols = 5;
        let pieceIdx = 0;
        let addEdges = (edges, cell1, cell2) => {
            if (edges[cell1] == null) {
                edges[cell1] = new Set();
            }
            edges[cell1].add(cell2);
            if (edges[cell2] == null) {
                edges[cell2] = new Set();
            }
            edges[cell2].add(cell1);
        }

        for (let i = 0; i < numRows; i++) {
            for (let j = 0; j < numCols; j++) {
                let id = `F${i}${j}`;
                let type;
                if (((i === 1 || i === 3) && (j === 1 || j === 3))|| (i=== 2 && j === 2)) {
                    type = 'camp';
                } else if (i === 5 && (j === 1 || j === 3)) {
                    type = 'base';
                } else {
                    type = 'normal';
                }
                let isOnRailroad = i === 0 || i === 4 || ((j === 0 || j === 4) && i !== 5);
                let piece = null;
                if (type !== 'camp') {
                    piece = {color: 'O', rank: defaultFormation[pieceIdx]};
                    pieceIdx++;
                }

                let cell = {
                    id, piece, type, isOnRailroad
                };
                formationCells[id] = cell;

                // add edges
                if (i > 0) {
                    addEdges(edges, id, `F${i-1}${j}`);
                }
                if (i < numRows - 1) {
                    addEdges(edges, id, `F${i+1}${j}`);
                }
                if (j > 0) {
                    addEdges(edges, id, `F${i}${j-1}`);
                }
                if (j < numCols - 1) {
                    addEdges(edges, id, `F${i}${j+1}`);
                }
                if (type === 'camp') {
                    [[i-1,j-1],[i-1,j+1],[i+1,j-1],[i+1,j+1]].forEach(idxs => addEdges(edges, id, `F${idxs[0]}${idxs[1]}`));
                }
            }
        }

        this.state = {
            cells: null,
            edges: null,
            railPaths: null,
            playerId: null,
            ownColor: null,
            currentTurnColor: null,
            lastMovePath: [],
            formationCells: formationCells,
            roadsDrawn: false,
            selectedCell: null,
            isConnected: false,
            ws: null
        };

        this.board = null;
        this.canvas = null;
        this.arrowCanvas = null;

        this.setBoardRef = element => {
            this.board = element;
        };

        this.setCanvasRef = element => {
            this.canvas = element;
        };

        this.setArrowCanvasRef = element => {
            this.arrowCanvas = element;
        }

        this.cellClicked = this.cellClicked.bind(this);
        this.drawRoads = this.drawRoads.bind(this);
        this.formationCellClicked = this.formationCellClicked.bind(this);
        this.joinGame = this.joinGame.bind(this);
    }

    cellClicked(clickedCell) {
        const isCellPieceSelectable = cell => {
            return cell.type !== "base" && cell.piece && cell.piece.rank !== 100 && this.isOwnPiece(cell.piece) && this.state.currentTurnColor === this.state.ownColor;
        };

        if (this.state.selectedCell) {
            let selectedCell = this.state.selectedCell;

            if (clickedCell.id === selectedCell.id) {
                this.setState({selectedCell: null});
                return;
            }

            if (this.isOwnPiece(clickedCell.piece)) {
                if (isCellPieceSelectable(clickedCell)) {
                    this.setState({selectedCell: clickedCell});
                }
            } else {

                // let isValidMove = this.isValidMove(clickedCell);
                // if (isValidMove) {
                //     console.log("valid");
                //     this.moveSelectedPieceToCell(clickedCell);
                //     this.setState({selectedCell: null});
                //     return;
                // }

                console.log('send');
                this.state.ws.send(JSON.stringify({
                    type: "move",
                    data: {
                        srcCellId: this.state.selectedCell.id,
                        destCellId: clickedCell.id
                    }
                }));
            }
        } else {
            if (isCellPieceSelectable(clickedCell)) {
                this.setState({selectedCell: clickedCell});
            }
        }
    }
    

    formationCellClicked(clickedCell) {
        if (this.state.selectedCell) {
            let selectedCell = this.state.selectedCell;

            if (clickedCell.id === selectedCell.id) {
                this.setState({selectedCell: null});
                return;
            }
            if (clickedCell.type === "camp") {
                return;
            }

            let srcRow = parseInt(selectedCell.id[1]);
            let clickedRow = parseInt(clickedCell.id[1]);
            if ((selectedCell.piece.rank === 10 && clickedRow === 0) || (clickedCell.piece.rank === 10 && srcRow === 0)) {
                return;
            }

            if ((selectedCell.piece.rank === 100 && clickedRow < 4) || (clickedCell.piece.rank === 100 && srcRow < 4)) {
                return;
            }

            if ((selectedCell.piece.rank === 0 && clickedCell.type !== 'base') || (clickedCell.piece.rank === 0 && selectedCell.type !== 'base')) {
                return;
            }

            let tmpPiece = selectedCell.piece
            let newFormation = {...this.state.formationCells,
                [selectedCell.id]: {...this.state}

            }

            this.setState(prevState => {
                let selectedPiece = selectedCell.piece
                let formationCells = {...prevState.formationCells,
                    [selectedCell.id]: {...prevState.formationCells[selectedCell.id], piece: clickedCell.piece},
                    [clickedCell.id]: {...prevState.formationCells[clickedCell.id], piece: selectedCell.piece}
                }
    
                return {selectedCell: null, formationCells};
            });

        } else {
            this.setState({selectedCell: clickedCell});
        }
    }

    isOwnPiece(piece) {
        return piece && piece.color === this.state.ownColor;
    }

    // use dfs to find path for engineer
    getRailPath(cells, edges, srcCell, destCell) {
        if (!srcCell.isOnRailroad || !destCell.isOnRailroad) {
            return null;
        }

        let parents = new Map();
        let queue = [srcCell];
        let visited = new Set([srcCell.id]);
        while (queue.length) {
            let n = queue.shift();

            let neighbours = edges[n.id];
            for (let id of neighbours) {
                if (id === destCell.id) {
                    parents.set(id, n.id);
                    let path = [];
                    let curr = destCell.id;
                    while (curr != null) {
                        path.unshift(curr);
                        curr = parents.get(curr);
                    }
                    console.log('asdfad', path, parents);
                    return path;
                }

                let cell = cells[id];
                if (cell.piece == null && cell.isOnRailroad && !visited.has(id)) {
                    visited.add(id);
                    queue.push(cell);
                    parents.set(id, n.id);
                }
            };
        }

        return null;
    }

    isValidMove(destCell) {
        let selectedCell = this.state.selectedCell;

        if (selectedCell.id === destCell.id) {
            return false;
        }

        if (destCell.piece && this.isOwnPiece(destCell.piece)) {
            return false;
        }


        if (selectedCell.isOnRailroad && destCell.isOnRailroad) {
            if (selectedCell.piece.rank === 1) {
                return this.getRailPath(selectedCell, destCell) != null;
            }

            let railPaths = this.state.railPaths;
            let path = railPaths.find(path => {
                return path.includes(selectedCell.id) && path.includes(destCell.id);
            });

            if (!path) {
                return false;
            }

            let idx1 = path.findIndex(id => id === selectedCell.id);
            let idx2 = path.findIndex(id => id === destCell.id);
            let startIdx = Math.min(idx1, idx2);
            let endIdx = Math.max(idx1, idx2);
            for (let i = startIdx + 1; i < endIdx; i++) {
                let currCell = this.state.cells[path[i]];
                if (currCell.piece != null) {
                    return false;
                }
            }

            return true;
        } else {
            return this.state.edges[selectedCell.id].has(destCell.id);
        }
    }

    comparePieces(piece1, piece2) {
        // bomb or same rank
        if (piece1.rank === 10 || piece2.rank === 10 || piece1.rank === piece2.rank) {
            return null;
        }

        // engineer vs mine
        if ((piece1.rank === 1 && piece2.rank === 100) || (piece1.rank === 100 && piece2.rank === 1)) {
            return piece1.rank === 1 ? piece1 : piece2;
        }

        return piece1.rank > piece2.rank ? piece1 : piece2;
    }

    moveSelectedPieceToCell(destCell) {
        this.setState(prevState => {
            let selectedCellId = prevState.selectedCell.id;
            let selectedPiece = prevState.selectedCell.piece;
            let destCellId = destCell.id;
            let cells = {...prevState.cells,
                [selectedCellId]: {...prevState.cells[selectedCellId], piece: null}
            };

            if (destCell.piece == null || this.comparePieces(selectedPiece, destCell.piece) === selectedPiece) {
                cells[destCellId] = {...prevState.cells[destCellId], piece: selectedPiece};
            } else if (this.comparePieces(selectedPiece, destCell.piece) == null) {
                cells[destCellId] = {...prevState.cells[destCellId], piece: null};
            }

            return {selectedCell: null, cells};
        });
    }

    getLastMovePath(cells,edges, lastMove) {
        if (!lastMove) {
            return [];
        }

        let {color, srcCellId, destCellId} = lastMove;
        let srcCell = cells[srcCellId];
        let destCell = cells[destCellId];
        console.log(srcCell, destCell);

        if (srcCell.isOnRailroad && destCell.isOnRailroad) {
            let path = this.getRailPath(cells, edges,srcCell, destCell);
            console.log(path);
            return path;
        } else {
            return [srcCellId, destCellId];
        }

    }

    isSelected(cell) {
        return this.state.selectedCell ? this.state.selectedCell.id === cell.id : false;
    }

    renderTopCells() {
        let cells = [];
        let numRows = 6;
        let numCols = 5;

        for (let i = numRows-1; i >= 0; i--) {
            for (let j = numCols -1; j >= 0; j--) {
                let cell = this.state.cells[`${oppositeColorMap[this.state.ownColor]}${i}${j}`];
                
                cells.push(
                    <BoardCell key={cell.id} cell={cell} isSelected={this.isSelected(cell)} cellClicked={this.cellClicked}></BoardCell>
                )
            }
        }

        return (
            <div className="player-territory top-territory">{cells}</div>
            );
     
    }
    
    renderLeftBottomRightCells() {
        let renderedSides = [];
        let classNameMap = {
            'W': "player-territory left-territory",
            'S': "player-territory bottom-territory",
            'E': "player-territory right-territory"
        };

        ['W', 'S', 'E'].forEach(dir => {
            let renderedCells = [];
            let numRows = 6;
            let numCols = 5;
            let color;
            if (dir === 'W') {
                color = leftColorMap[this.state.ownColor];
            } else if (dir === 'E') {
                color = rightColorMap[this.state.ownColor];
            } else {
                color = this.state.ownColor;
            }

            for (let i = 0; i < numRows; i++) {
                for (let j = 0; j < numCols; j++) {
                    let cell = this.state.cells[`${color}${i}${j}`];

                    renderedCells.push(
                        <BoardCell key={cell.id} cell={cell} isSelected={this.isSelected(cell)} cellClicked={this.cellClicked}></BoardCell>
                    )
                }
            }
            renderedSides.push(<div className={classNameMap[dir]}>{renderedCells}</div>);
        });

        return renderedSides;
    }

    renderCenterCells() {
        let cells = [];
        const idxOrderMap = {
            'O': [0,1,2,3,4,5,6,7,8,],
            'B': [2,5,8,1,4,7,0,3,6],
            'G': [8,7,6,5,4,3,2,1,0],
            'P': [6,3,0,7,4,1,8,5,2]
        }
        let idxOrder = idxOrderMap[this.state.ownColor];

        for (let i = 0; i < 9; i++) {
            let idx = idxOrder[i];
            let cell = this.state.cells[`M${idx}`];
            cells.push(
                <BoardCell key={cell.id} cell={cell} isSelected={this.isSelected(cell)} cellClicked={this.cellClicked}></BoardCell>
            )
            if (i === 2 || i === 5) {
                for (let j = 0; j < 5; j++) {
                    cells.push(<div key={`blank${idx}${j}`}></div>);
                }
            } else if (i !== 8) {
                cells.push(<div key={`blank${idx}`}></div>);
            }

        }

        return (
            <div className="center-territory">{cells}</div>
        )
    }

    renderFormationCells() {

        let renderedCells = [];
        let numRows = 6;
        let numCols = 5;

        for (let i = 0; i < numRows; i++) {
            for (let j = 0; j < numCols; j++) {
                let cell = this.state.formationCells[`F${i}${j}`];

                renderedCells.push(
                    <BoardCell key={cell.id} cell={cell} isSelected={this.isSelected(cell)} cellClicked={this.formationCellClicked}></BoardCell>
                )
            }
        }
        return <div className="player-territory">{renderedCells}</div>


    }

    createLineBetweenCells(ctx, cell1, cell2, isRail, defaultWidth) {
        let boardRect = this.board.getBoundingClientRect();
        let elem1 = document.getElementById(cell1);
        let elem2 = document.getElementById(cell2);
        let rect1 = elem1.getBoundingClientRect();
        let rect2 = elem2.getBoundingClientRect();
        let x1 = (rect1.left + rect1.right)/2 - boardRect.left;
        let y1 = (rect1.top + rect1.bottom)/2 - boardRect.top;
        let x2 = (rect2.left + rect2.right)/2 - boardRect.left;
        let y2 = (rect2.top + rect2.bottom)/2 - boardRect.top;

        if (!isRail) {

            ctx.beginPath();        
            ctx.strokeStyle = "rgb(144,180,104)";
            ctx.lineWidth = 3 * (boardRect.width/defaultWidth);
            ctx.setLineDash([]);
            ctx.moveTo(x1,y1);
            ctx.lineTo(x2,y2);
            ctx.stroke();
        } else {

            ctx.beginPath();        
            ctx.strokeStyle = railOuterColor;
            ctx.lineWidth = 10 * (boardRect.width/defaultWidth);
            ctx.setLineDash([]);
            ctx.moveTo(x1,y1);
            ctx.lineTo(x2,y2);
            ctx.stroke();

            ctx.beginPath();
            ctx.strokeStyle = railInnerColor;
            ctx.lineWidth = 5 * (boardRect.width/defaultWidth);
            ctx.setLineDash([2,2]);
            ctx.moveTo(x1,y1);
            ctx.lineTo(x2,y2);
            ctx.stroke();
        }
    }

    createArcRail(ctx, cell1, cell2) {
        let boardRect = this.board.getBoundingClientRect();
        let elem1 = document.getElementById(cell1);
        let elem2 = document.getElementById(cell2);
        let rect1 = elem1.getBoundingClientRect();
        let rect2 = elem2.getBoundingClientRect();
        let x1 = (rect1.left + rect1.right)/2 - boardRect.left;
        let y1 = (rect1.top + rect1.bottom)/2 - boardRect.top;
        let x2 = (rect2.left + rect2.right)/2 - boardRect.left;
        let y2 = (rect2.top + rect2.bottom)/2 - boardRect.top;
        console.log(this.board.offsetWidth, this.board.offsetHeight);
        ctx.beginPath();        
        ctx.strokeStyle = railOuterColor;
        ctx.lineWidth = 10 * (boardRect.width/DEFAULT_BOARD_WIDTH);
        ctx.setLineDash([]);
        ctx.moveTo(x1,y1);
        ctx.arcTo(x1, y2, x2, y2, 60* (boardRect.width/DEFAULT_BOARD_WIDTH));
        ctx.stroke();


        ctx.beginPath();
        ctx.strokeStyle = railInnerColor;
        ctx.lineWidth = 5 * (boardRect.width/DEFAULT_BOARD_WIDTH);
        ctx.setLineDash([2,2]);
        ctx.moveTo(x1,y1);
        ctx.arcTo(x1, y2, x2, y2, 60 * (boardRect.width/DEFAULT_BOARD_WIDTH));
        ctx.stroke();
    }

    drawArrowHead(ctx, fromX, fromY, toX, toY) {
        let headlen = 10;
        let theta = 45;
        let arrowX, arrowY;//Arrow line end point coordinates

        let angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI;
        let angle1 = (angle + theta) * Math.PI / 180;
        let angle2 = (angle - theta) * Math.PI / 180;
        let topX = headlen * Math.cos(angle1);
        let topY = headlen * Math.sin(angle1);
        let botX = headlen * Math.cos(angle2);
        let botY = headlen * Math.sin(angle2);
        ctx.beginPath();
        // draw a straight line
        // ctx.moveTo(fromX, fromY);
        // ctx.lineTo(toX, toY);
    
        arrowX = toX + topX;
        arrowY = toY + topY;
        //Draw the upper arrow line
        ctx.moveTo(arrowX, arrowY);
        ctx.lineTo(toX, toY);
    
        arrowX = toX + botX;
        arrowY = toY + botY;
        //Draw the arrow line below
        ctx.lineTo(arrowX, arrowY);
    
        ctx.lineWidth = "4";
        ctx.strokeStyle = "rgb(208,180,24)";
        ctx.stroke();
    }

    drawLastMovePath(lastMovePath) {
        if (lastMovePath) {
            let boardRect = this.board.getBoundingClientRect();
            let ctx = this.arrowCanvas.getContext("2d");
            this.arrowCanvas.style.width = '100%';
            this.arrowCanvas.style.height = '100%';
            this.arrowCanvas.width  = this.arrowCanvas.offsetWidth;
            this.arrowCanvas.height = this.arrowCanvas.offsetHeight;
            ctx.clearRect(0, 0, this.arrowCanvas.width, this.arrowCanvas.height);
            for (let i = 0; i < lastMovePath.length - 1; i++) {
                console.log('drawwing')
                let currCellId = lastMovePath[i];
                let nextCellId = lastMovePath[i+1];
                let elem1 = document.getElementById(currCellId);
                let elem2 = document.getElementById(nextCellId);
                console.log(elem1, elem2);
                let rect1 = elem1.getBoundingClientRect();
                let rect2 = elem2.getBoundingClientRect();
                let x1 = (rect1.left + rect1.right)/2 - boardRect.left;
                let y1 = (rect1.top + rect1.bottom)/2 - boardRect.top;
                let x2 = (rect2.left + rect2.right)/2 - boardRect.left;
                let y2 = (rect2.top + rect2.bottom)/2 - boardRect.top;
                // this.drawArrowHead(ctx,x1-(x2-x1),y1 -(y2-y1),x1,y1);

                let headlen = 10 * (boardRect.width/DEFAULT_BOARD_WIDTH);
                console.log('head',headlen);

                let theta = 45;
                let arrowX, arrowY;//Arrow line end point coordinates
                console.log(x2,x1, y2,y1);
                let offset = headlen * Math.cos(theta) / 2;

                let fromX = x1 - (x2-x1);
                let fromY = y1 - (y2-y1);
                let toX = x1;
                let toY = y1;

                if (Math.abs(toX-fromX) > 1) {
                    toX += x2 > x1 ? offset: -offset;
                }
                if (Math.abs(toY-fromY) > 1) {
                    toY += y2 > y1 ? offset: -offset;
                }

                let angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI;
                let angle1 = (angle + theta) * Math.PI / 180;
                let angle2 = (angle - theta) * Math.PI / 180;
                let topX = headlen * Math.cos(angle1);
                let topY = headlen * Math.sin(angle1);
                let botX = headlen * Math.cos(angle2);
                let botY = headlen * Math.sin(angle2);
                ctx.beginPath();
                // draw a straight line
                // ctx.moveTo(fromX, fromY);
                // ctx.lineTo(toX, toY);
            
                arrowX = toX + topX;
                arrowY = toY + topY;
                //Draw the upper arrow line
                ctx.moveTo(arrowX, arrowY);
                ctx.lineTo(toX, toY);
            
                arrowX = toX + botX;
                arrowY = toY + botY;
                //Draw the arrow line below
                ctx.lineTo(arrowX, arrowY);
            
                ctx.lineWidth = 4 * (boardRect.width/DEFAULT_BOARD_WIDTH);
                console.log(4 * (boardRect.width/DEFAULT_BOARD_WIDTH));
                ctx.strokeStyle = "rgb(208,180,24)";
                ctx.stroke();

            }
        }
    }

    drawRoads() {
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';
        this.canvas.width  = this.canvas.offsetWidth;
        this.canvas.height = this.canvas.offsetHeight;
        let ctx = this.canvas.getContext("2d");

        let addedEdges = new Set();
        let curvedEdges = new Set();
        
        let ownColor = this.state.ownColor;
        let leftColor = leftColorMap[ownColor];
        let rightColor = rightColorMap[ownColor];
        let topColor = oppositeColorMap[ownColor];

        let corners1 = [`${ownColor}00`, `${topColor}04`, `${topColor}00`, `${ownColor}04`];
        let corners2 = [`${leftColor}04`, `${leftColor}00`, `${rightColor}04`, `${rightColor}00`];
        for (let i = 0 ; i < 4; i++) {
            let cell1 = corners1[i];
            let cell2 = corners2[i];
            curvedEdges.add(`${cell1}${cell2}`);
            curvedEdges.add(`${cell2}${cell1}`);
            this.createArcRail(ctx, cell1, cell2);
        }

        for (let cell1 in this.state.edges) {
            let edges = this.state.edges[cell1];
            edges.forEach(cell2 => {
                if (!curvedEdges.has(`${cell1}${cell2}`) && !addedEdges.has(`${cell2}${cell1}`)) {
                    let cells = this.state.cells;
                    let isRail = cells[cell1].isOnRailroad && cells[cell2].isOnRailroad;
                    this.createLineBetweenCells(ctx, cell1, cell2, isRail, DEFAULT_BOARD_WIDTH)
                    addedEdges.add(`${cell1}${cell2}`)
                }
            });
        }

        this.setState({roadsDrawn: true});
    }

    drawFormationRoads() {
        this.canvas.style.width = '100%';
        this.canvas.style.height = '100%';
        this.canvas.width  = this.canvas.offsetWidth;
        this.canvas.height = this.canvas.offsetHeight;
        let ctx = this.canvas.getContext("2d");

        let addedEdges = new Set();
        console.log('draw')

        for (let cell1 in this.state.edges) {
            let edges = this.state.edges[cell1];
            edges.forEach(cell2 => {
                if (!addedEdges.has(`${cell2}${cell1}`)) {
                    let cells = this.state.formationCells;
                    let isRail = cells[cell1].isOnRailroad && cells[cell2].isOnRailroad;
                    this.createLineBetweenCells(ctx, cell1, cell2, isRail, DEFAULT_FORMATION_WIDTH);
                    addedEdges.add(`${cell1}${cell2}`)
                }
            });
        }

        this.setState({roadsDrawn: true});
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.state.isConnected && !prevState.isConnected && !this.state.roadsDrawn) {
            this.drawRoads();
        }
        if (this.state.isConnected && prevState.lastMovePath !== this.state.lastMovePath) {
            this.drawLastMovePath(this.state.lastMovePath);
        }
    }

    componentDidMount() {
        this.drawFormationRoads();
    }

    joinGame(type) {
        fetch('https://junqi-server.herokuapp.com/connect', {method: 'POST', credentials: 'include'}).then(res => {
            return res.json();
        }).then(data => {
            let playerId = data.playerId;
            const ws = new WebSocket('wss://junqi-server.herokuapp.com');


            let formation = [];
            for (let cellId in this.state.formationCells) {
                let cell = this.state.formationCells[cellId];
                if (cell.piece) {
                    formation.push(cell.piece.rank);
                }
            }

            ws.onopen = event => {
                ws.send(JSON.stringify({
                    type: 'join',
                    data: {gameType: type, formation}
                }));
                console.log('sending', 'ugh');
            };

            ws.onmessage = event => {
                let {type, data} = JSON.parse(event.data);
                if (type === "gamestate") {
                    if (data.gameResult.isFinished) {
                        if (data.gameResult.winners.length) {
                            alert(data.gameResult.winners.includes(data.playerColors[playerId]) ? "you win :V" : "you lose :v");
                        } else {
                            alert("draw");
                        }

                        this.state.ws.close();

                        this.setState({
                            cells: null,
                            edges: null,
                            railPaths: null,
                            playerId: null,
                            ownColor: null,
                            currentTurnColor: null,
                            lastMovePath: [],
                            roadsDrawn: false,
                            selectedCell: null,
                            isConnected: false,
                            ws: null
                        });
                    } else {
                        let edges = {};
                        if (this.state.edges == null) {
                            for (let k in data.board.edges) {
                                edges[k] = new Set(Object.keys(data.board.edges[k]));
                            }
                        } else {
                            edges = this.state.edges;
                        }

                        let lastMovePath = this.getLastMovePath(data.board.cellStates, edges, data.moveHistory[data.moveHistory.length - 1]);
                        this.setState({
                            playerId,
                            cells: data.board.cellStates,
                            edges: edges,
                            railPaths: data.board.railPaths,
                            ownColor: data.playerColors[playerId],
                            currentTurnColor: data.currentTurnColor,
                            lastMovePath: lastMovePath,
                            isConnected: true,
                            roadsDrawn: false,
                            selectedCell: null,
                            ws
                        });
                    }
                } else if (type === "invalidmove") {
                    console.log("invalid move");
                }

            };

            this.setState({ws});
        }); 

    }

    render() {
        if (!this.state.isConnected) {
            let formationCells = this.renderFormationCells();
            return (
            <div className="lobby-container">
                <div className="button-container">
                    <button onClick={()=>this.joinGame("normal")}>Four player game</button>
                    <button onClick={()=>this.joinGame("onevsai")}>Single player game</button>
                    <button onClick={()=>this.joinGame("twoplayer")}>Two player game</button>
                </div>
                <div className="formation-container" ref={this.setBoardRef}>
                    {formationCells}
                    <canvas ref={this.setCanvasRef} style={{position:"absolute", top:0, left: 0, pointerEvents: 'none'}}></canvas>
                </div>
            </div>);
        }
        let topSide = this.renderTopCells();
        let [leftSide, bottomSide, rightSide] = this.renderLeftBottomRightCells();
        let center = this.renderCenterCells();
        return ( 
            <div className="board-container">
                <div className="board" ref={this.setBoardRef}>
                    <div className="top-container">{topSide}</div>
                    <div className="middle-container">{leftSide}{center}{rightSide}</div>
                    <div className="bottom-container">{bottomSide}</div>
                    <canvas ref={this.setCanvasRef} style={{position:"absolute", top:0, left: 0, pointerEvents: 'none'}}></canvas>
                    <canvas ref={this.setArrowCanvasRef} style={{position:"absolute", top:0, left: 0, zIndex:2, pointerEvents: 'none'}}></canvas>
                </div>
            </div>
        );
    }
}

