import React from 'react';
import './BoardCell.css'; 
import Piece from './Piece';

export default class BoardCell extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        let renderedPiece;
        let cell = this.props.cell;
        let piece = cell.piece;

        if (piece) {
            renderedPiece = <Piece piece={piece} isSelected={this.props.isSelected}></Piece>
        }

        let shape;
        let svgClassName = "cell-svg";
        if (cell.type === "normal") {
            shape = <rect width="100%" height="100%"rx="4px" ry="4px" strokeWidth="4px" fill="rgb(65,68,37)" stroke="rgb(104,144,176)"/>;
        } else if (cell.type === "camp") {
            shape = <circle r="60%" cx="50%" cy="50%" strokeWidth="4px" fill="rgb(65,68,37)" stroke="rgb(192,144,88)"/>;
        } else if (cell.type === "base") {
            shape = <circle r="40%" cx="50%" cy="50%" strokeWidth="4px" fill="rgb(65,68,37)" stroke="rgb(144,180,104)"/>;
        } else if (cell.type === "center") {
            shape = <rect width="100%" height="100%" rx="4px" ry="4px" strokeWidth="4px" fill="rgb(65,68,37)" stroke="rgb(104,144,176)"/>;
            svgClassName += " center-svg";
        }

        let containerClassName = "cell-container";
        // console.log(this.props.isInLastPath);
        if (this.props.isInLastPath) {
            // containerClassName += " in-last-move";
        }

        return (
            <div className={containerClassName} onClick={()=>this.props.cellClicked(cell)}>
                {renderedPiece || cell.id}
                <svg id={cell.id} className={svgClassName}>
                    {shape}
                </svg>
            </div>
        );
    }
}