let colorMap = {
    'O': 'orange',
    'B': 'blue',
    'G': "green",
    'P': 'purple'
}

export default class Piece extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        let isEn = true;
        let piece = this.props.piece;
        let className = `piece ${colorMap[piece.color]}`;
        if (this.props.isSelected) {
            className += " selected";
        }
        if(isEn){
            className += " en";
        }

        return <div className={className}>{piece.rank != null && enPieceNameMap[piece.rank]}</div>
    }
}